.vector-icon {
  position: relative;
  width: 79px;
  height: 74px;
}
.home {
  text-decoration: none;
  position: relative;
  line-height: 30px;
  font-weight: 500;
  color: inherit;
}
.home-parent,
.vector-parent {
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}
.home-parent {
  width: 303px;
  padding: var(--padding-3xs);
}
.vector-parent {
  align-self: stretch;
  height: 94px;
  padding: var(--padding-3xs) var(--padding-3xs) var(--padding-3xs) 17px;
  max-width: 1440px;
}
.your-trusted-partner {
  align-self: stretch;
  position: relative;
  line-height: 70.5px;
  font-weight: 500;
  display: inline-block;
  height: 282px;
  flex-shrink: 0;
}
.for-an-accounting {
  align-self: stretch;
  position: relative;
  font-size: 24px;
  line-height: 34px;
  font-weight: 200;
  color: var(--color-black);
}
.your-trusted-partner-for-accou-parent {
  align-self: stretch;
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-xl) 34px var(--padding-xl) 27px;
  align-items: flex-start;
  justify-content: space-between;
}
.frame-child {
  align-self: stretch;
  flex: 1;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.body,
.desktop-1,
.frame-parent {
  overflow: hidden;
  display: flex;
  box-sizing: border-box;
  justify-content: flex-start;
}
.frame-parent {
  align-self: stretch;
  flex-direction: row;
  padding: 63px var(--padding-3xs) 66px 11px;
  align-items: flex-start;
  gap: 30px;
  max-width: 1440px;
  text-align: left;
  font-size: 64px;
  color: rgba(0, 0, 0, 0.7);
  font-family: var(--font-inter);
}
.body,
.desktop-1 {
  flex-direction: column;
}
.body {
  flex: 1;
  width: 1220px;
  padding: var(--padding-3xs);
  align-items: flex-start;
  gap: var(--gap-3xs);
}
.desktop-1 {
  position: relative;
  background-color: #fff;
  width: 100%;
  height: 1064px;
  padding: 26px var(--padding-3xs) var(--padding-3xs);
  align-items: center;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-mulish);
}
@media screen and (max-width: 1200px) {
  .vector-parent {
    max-width: 1440px;
  }
  .your-trusted-partner {
    align-self: stretch;
    width: auto;
    flex: 1;
  }
  .your-trusted-partner-for-accou-parent {
    flex: 1;
    align-self: stretch;
    height: auto;
  }
  .frame-parent {
    max-width: 1440px;
  }
  .body {
    align-self: stretch;
    width: auto;
    flex: 1;
  }
}
@media screen and (max-width: 960px) {
  .your-trusted-partner {
    font-size: 34px;
    line-height: 50px;
    align-self: stretch;
    width: auto;
    flex: 1;
  }
  .for-an-accounting {
    font-size: 18px;
    line-height: 24px;
  }
  .your-trusted-partner-for-accou-parent {
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: var(--padding-xl);
    box-sizing: border-box;
    flex: unset;
    align-self: stretch;
  }
  .frame-child {
    flex: unset;
    align-self: stretch;
    height: auto;
    max-width: 500px;
  }
  .frame-parent {
    flex-direction: column;
    gap: var(--gap-3xs);
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: var(--padding-3xs);
    box-sizing: border-box;
  }
  .body {
    align-self: stretch;
    width: auto;
    flex: 1;
  }
}
